import {queryJsonBill, doAction, queryTaskActions,queryApproveDetail} from '../api'
import {Indicator, Toast} from 'mint-ui'

import {
  UPDATE_TASKBILL,
  UPDATE_TASKBUTTON,
  UPDATE_COMMITUSER,
  UPDATE_MESSAGEATTACHMENTLIST,
  UPDATE_DEFALUT
} from './mutation-type'

export default {
  async queryJsonBill({commit}, params) {
    Indicator.open()
    const jsonbill = await queryJsonBill(params)
    let resultData = jsonbill.taskBill//表单明细
    let resultButton = jsonbill.taskAction//表单按钮
    let resultApproveDetail = jsonbill.approveDetail//表单审批
    let resultMessageAttachmentList = jsonbill.messageAttachmentList//表单附件
    commit(UPDATE_TASKBILL, {resultData})
    commit(UPDATE_TASKBUTTON, {resultButton})
    commit(UPDATE_COMMITUSER, {resultApproveDetail})
    commit(UPDATE_MESSAGEATTACHMENTLIST, {resultMessageAttachmentList})
    commit(UPDATE_DEFALUT, {params})
    Indicator.close()
  },
  async doAction({commit}, params) {
    Indicator.open()
    const resultData = await doAction(params)
    Toast(resultData.des)
    const resultButton = await queryTaskActions(params)
    const resultApproveDetail = await queryApproveDetail(params)
    commit(UPDATE_TASKBUTTON, {resultButton})
    commit(UPDATE_COMMITUSER,{resultApproveDetail})
    Indicator.close()
  },
}



