import {
  UPDATE_TASKBILL,
  UPDATE_TASKBUTTON,
  UPDATE_COMMITUSER,
  UPDATE_MESSAGEATTACHMENTLIST,
  UPDATE_DEFALUT,
} from './mutation-type'

export default {
  [UPDATE_TASKBILL](state, {resultData}) {
    state.taskbillResult = resultData
    state.listHead = resultData.taskbill.listHead.reverse()
  },
  [UPDATE_TASKBUTTON](state, {resultButton}) {
    state.listButtons = resultButton.listActions
  },
  [UPDATE_COMMITUSER](state, {resultApproveDetail}) {
    let commitUser = {
      makername: resultApproveDetail.makername,
      psnid: resultApproveDetail.psnid,
      submitdate: resultApproveDetail.submitdate.substring(0, 10)
    }
    state.commitUser = commitUser

    if (resultApproveDetail.approvehistorylinelist) {
      let approveUsers = resultApproveDetail.approvehistorylinelist.map((item) => ({
        action: item.action,
        approvedid: item.approvedid,
        handledate: item.handledate.substring(0, 10),
        handlername: item.handlername,
        note: item.note,
        psnid: item.psnid
      }))
      state.approveUsers = approveUsers
    }
  },
  [UPDATE_MESSAGEATTACHMENTLIST](state, {resultMessageAttachmentList}) {
    if (resultMessageAttachmentList.flag === '0') {
      let messageAttachmentListValue = resultMessageAttachmentList.count
      let attachstructlist = resultMessageAttachmentList.attachstructlist

      attachstructlist[0].attachmentgrouplist.forEach((attachmentgroup)=>{
        let fileIds = attachmentgroup.fileid.split('&#%')
        attachmentgroup['fileid'] = fileIds.join(',')
      })

      state.messageAttachmentListValue = messageAttachmentListValue
      state.attachstructlist = attachstructlist
    }
  },
  [UPDATE_DEFALUT](state, {params}) {
    state.userId = params.userId
    state.taskId = params.taskId
  }
}



