/*
包含n个接口请求函数的模块
函数的返回值: promise对象
 */
import ajax from './ajax'

//const BASE_URL = '/api'
const BASE_URL = 'http://27.17.37.154:8000'

//查询任务明细
export const queryJsonBill = (params) => ajax(BASE_URL + '/taskbill/jsonbill?userId=' + params.userId + '&taskId=' + params.taskId)
//执行按钮动作
export const doAction = (params) => ajax(BASE_URL + '/taskbill/' + params.actionCode + '?userId=' + params.userId + '&taskId=' + params.taskId + '&note=' + params.note)
//查询界面按钮
export const queryTaskActions = (params) => ajax(BASE_URL + '/taskbill/getTaskActions?taskId=' + params.taskId)
//查询审批历史
export const queryApproveDetail = (params) => ajax(BASE_URL + '/taskbill/getApproveDetail?userId=' + params.userId + '&taskId=' + params.taskId)



