import Vue from 'vue'
import VueRouter from 'vue-router'

import BillCard from '../pages/BillCard'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [{
    name: 'billCard',
    path: '/billCard',
    component: BillCard,
    meta: {
      keepAlive: true // 需要被缓存
    }
  }, {
    path: '/',
    redirect: '/billCard',
    meta: {
      keepAlive: true // 需要被缓存
    }
  }]
})




