<template>
  <div class="um-win">
    <div class="um-content">
      <mt-navbar v-model="selectTab">
        <mt-tab-item id="1">单据信息</mt-tab-item>
        <mt-tab-item id="2">审批历史</mt-tab-item>
        <mt-tab-item id="3">附件</mt-tab-item>
      </mt-navbar>
      <div class="tab-container"></div>
      <mt-tab-container v-model="selectTab">
        <mt-tab-container-item id="1">
          <ul class="um-list">
            <li v-for="(headData,index) in listHead">
              <div class="um-list-item">
                <div class="um-list-item-inner" style="height: 50px;">
                  <div class="um-list-item-left pl15">
                    {{headData.itemName}}
                  </div>
                  <div class="um-list-item-right">
                    {{headData.itemValue}}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
          <div class="um-list">
            <div class="um-process">
              <a href="#" class="um-list-item" :style="borderLeft">
                <div class="um-list-item-inner">
                  <div class="um-list-item-body ml15">
                    <p class="f14">提交人：<span class="fb">{{commitUser.makername}}</span></p>
                    <p class="um-gray um-text-overflow2 f12 mt5">{{commitUser.submitdate}}</p>
                  </div>
                </div>
              </a>
            </div>
            <div class="um-process" v-for="(approveUser,index) in approveUsers" :key="index">
              <a href="#" class="um-list-item" :style="index===approveUsers.length-1?NONEborderLeft:borderLeft">
                <div class="um-list-item-inner">
                  <div class="um-list-item-body ml15">
                    <p class="f14">审批人：<span class="fb">{{approveUser.handlername}}</span><span
                      class="ml10 um-green">{{approveUser.action}}</span></p>
                    <p class="f14 mt5">意&nbsp;&nbsp;&nbsp;&nbsp;见：<span class="um-blue">{{approveUser.note}}</span></p>
                    <p class="um-gray um-text-overflow2 f12 mt5">{{approveUser.handledate}}</p>
                  </div>
                </div>
              </a>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
          <div v-for="(attachstruct,index) in attachstructlist" :key="index">
            <span style="font-size: 16px;color: dodgerblue;margin-left: 10px">{{attachstruct.attachmentgroupname}}</span>
            <p style="height: 10px"></p>
            <mt-cell v-for="(attachmentgroup,index) in attachstruct.attachmentgrouplist"
                     :title="attachmentgroup.filename" :label="attachmentgroup.filesize" :is-link=true
                     :to="fileURL+attachmentgroup.fileid" target="_blank">
            </mt-cell>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>


    <div class="um-footer">
      <div class="btn-group btn-group-justify">
        <button v-for="(ncButton,index) in listButtons" :key="ncButton.code" class="btn um-btn-info" type="button"
                @click="doAction(ncButton.code,ncButton.name)">{{ncButton.name}}
        </button>
      </div>
      <!-- <mt-button v-for="(ncButton,index) in listButtons" :key="ncButton.code"
                  @click="doAction(ncButton.code,ncButton.name)">
         <span style="color: #2ba2fa;font-size: 18px;font-weight: bold;">{{ncButton.name}}</span>
       </mt-button>-->
    </div>

  </div>
</template>

<script>

  import {mapState, mapActions} from 'vuex'
  import {MessageBox} from 'mint-ui'

  export default {
    data() {
      return {
        popupVisible: false,
        popupValue: '',
        popupName: '',
        selectTab: '1',
        borderLeft: 'border-left: 2px dotted green;',
        NONEborderLeft: 'border-left:none;',
        fileURL:'http://27.17.37.154:8000/taskbill/queryFile?fileId='
      }
    },
    computed: {
      ...mapState(['listHead', 'listButtons', 'commitUser', 'approveUsers', 'messageAttachmentListValue', 'attachstructlist'])
    },
    mounted() {
      var params = {'userId': this.$route.query.userId, 'taskId': this.$route.query.taskId}
      this.queryJsonBill(params)
    },
    methods: {
      ...mapActions(['queryJsonBill']),
      doAction(actionCode, actionName) {
        let intputValue = ''
        if (actionCode === 'doAgree') {
          intputValue = actionName
        }
        MessageBox.prompt('请输入批语', actionName, {inputValue: intputValue}).then(({value, action}) => {
          var params = {
            'userId': this.$route.query.userId,
            'taskId': this.$route.query.taskId,
            'actionCode': actionCode,
            'note': value + '--来自 new NC mobile'
          }
          this.$store.dispatch('doAction', params)
        })
      }
    },
  }
</script>

<style scoped>
  .tab-container {
    margin-top: 10px;
  }

  p {
    padding: 2px;
  }

  .um-process {
    margin-left: 15px;
  }

  .um-process .um-list-item :before {
    content: none !important;
  }

  .um-process .um-list-item :after {
    content: url("../../static/img/marker5.jpg");
    position: absolute;
    left: -12px;
    top: 2px;
    width: 14px;
  }


  .btn {
    margin: 0px 5px 0px 5px;
    border-radius: 5px;
  }

  .um-footer {
    margin-bottom: 10px;
    margin-left: 5px;
  }

</style>



